.empty {
	text-align: center;
	margin-top: 3rem;
}

.empty h2 {
	font-size: 1.25rem;
	color: #cecece;
	font-family: var(--font-raleway);
}

.container {
	/* background: red; */
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	margin-top: 3rem;
}

.movie {
  width: 310px;
  height: 460px;
  margin: 1.5rem;
  position: relative;
  border-radius: 12px;
  overflow: hidden;
  border: none;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  /* box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1); */
}

.movie:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

/* Year Div */
.movie div:nth-of-type(1) {
  position: absolute;
  top: 0;
  padding: 16px;
  width: 100%;
  opacity: 0;
  color: #000000;
  font-weight: bolder;
}

/* Image Div */
.movie div:nth-of-type(2) {
	width: 100%;
	height: 100%;
}

.movie div:nth-of-type(2) img {
	width: 100%;
	height: 100%;
}

/* Info Div */
.movie div:nth-of-type(3) {
  z-index: 2;
  background-color: rgba(248, 248, 248, 0.9);
  padding: 16px 24px 24px;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.movie div:nth-of-type(3) span {
  font-family: "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #313131;
}

.movie div:nth-of-type(3) h3 {
  margin-top: 5px;
  font-family: var(--font-roboto), serif;
  color: #252525;
}

.movie:hover div:nth-of-type(2) {
	 height: 100%;
  opacity: 0.6;
}

/* .movie:hover div:nth-of-type(3) {
  background-color: transparent;
} */

.movie:hover div:nth-of-type(1) {
	z-index: 4;
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .app {
    padding: 4rem 2rem;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .container {
    margin-top: 2rem;
  }

  .movie {
    width: "100%";
    margin: 1rem;
  }
}